import React, {useEffect, useRef, useState} from 'react';
import './AboutUs.css';
import Header from "../../components/Header/Header";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import {handleGetDataHomePage} from "../../service/drinks";
import Footer from "../../components/Footer/Footer";
import { FaArrowLeft, FaArrowRight } from 'react-icons/fa';

const AboutUs = () => {

    const [timelineData, setTimelineData] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await handleGetDataHomePage();
                const timelineData = response.find(name => name.name === "timeline").data;
                setTimelineData(timelineData);
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, []);
    return (
        <div>
            <Header/>
            <Banner/>
            <div className="about-us-page">
                <AboutUsElement/>
                <VisionMission/>
                <CoreValues/>
                <Achievements/>
                <Timeline timelineData={timelineData}/>
            </div>
            <Footer/>
        </div>
    );
};

const Banner = () => (
    <div className="banner_page">
        <img src="https://phela.vn/wp-content/uploads/2021/07/banner-web-.jpg" alt="Banner" className="banner_image"/>
        <div className="banner_text">
            <h2>V PH LA</h2>
        </div>
    </div>
);

const AboutUsElement = () => (
    <div className="about-us-element">
        <div className="text">
            <h6>Nt Hng c Sn</h6>
            <p style={{fontWeight: "lighter"}}>
                Ph La lun trn qu, nng niu nhng gi tr Nguyn Bn  mi vng t m chng ti i qua, ni tm hn
                c ng iu vi thin nhin, vi ni vt v nhc nhn ca ngi nng dn; cm nhn c ht thy
                nhng tng hng n su trong tng nguyn liu.
            </p>
            <p style={{fontWeight: "500"}}>
                Mt chng ng di ang ch pha trc, Ph La  sn sng vit tip cu chuyn Nt Hng c Sn 
                Nguyn Bn  Th Cng y cm hng v cng t ho hn khi c mang s mnh: nh thc nhng nt hng
                c sn ca nng sn Vit Nam.
            </p>
        </div>
        <div className="img">
            <img src="https://phela.vn/wp-content/uploads/2021/07/HH_3783-600x400.jpg" alt="Cups"/>
        </div>
    </div>
);

const VisionMission = () => (
    <div className="vision-mission-section">
        <div className="vision-container">
            <div className="vision-icon">
                <img src="https://phela.vn/wp-content/uploads/2021/09/eyes.png" alt="Tm nhn"/>
            </div>
            <h4>Tm nhn</h4>
            <p>Mang ngun nng sn cao cp ca Vit Nam tip cn gn hn vi mi ngi v vn ra th gii.</p>
        </div>
        <div className="mission-container">
            <div className="mission-icon">
                <img src="https://phela.vn/wp-content/uploads/2021/09/Dm.png" alt="S mnh"/>
            </div>
            <h4>S mnh</h4>
            <p>ng hnh cng ngi nng dn trong qu trnh sn xut v pht trin bn vng ngun nguyn liu c
                sn.</p>
        </div>
    </div>
);

const CoreValues = () => (
    <div className="core-values">
        <div className="line"></div>
        <div className="section">
            <div className="text">
                <h4>Th Cng</h4>
                <p>
                    Ti Ph La, s tm huyt, t m v tinh t c th hin qua tng
                    sn phm. Nhng l tr  long c thu hoch v s ch th cng,
                    kt hp cng vic nghin cu v sng to, Ph La  lm nn nhng
                    sn phm mang hng v nguyn bn v chn tht nht.
                </p>
            </div>
            <div className="image">
                <img
                    src="https://phela.vn/wp-content/uploads/2023/04/326386567_2808509185946106_3255994807608296453_n.jpg"
                    alt="Th Cng"
                />
                <div className="dot"></div>
            </div>
        </div>
        <div className="section reverse">
            <div className="image">
                <img
                    src="https://phela.vn/wp-content/uploads/2023/04/336169980_215934231017041_7772223356356948632_n.jpg"
                    alt="Cm Hng"
                />
                <div className="dot"></div>
            </div>
            <div className="text">
                <h4>Cm Hng</h4>
                <p>
                    Mi sn phm, chin dch ca Ph La u c xy dng da theo
                    nhng ngun cm hng rt i bnh d trong cuc sng. Vi Ph La,
                     l ni bt ngun ca nhng iu mi m v kt ni nhng tm hn
                    ng iu nhng vn mang c tnh ring bit.
                </p>
            </div>
        </div>
        <div className="section">
            <div className="text">
                <h4>Quan Tm</h4>
                <p>
                    Ti u tri nghim ca tng khch hng l tiu ch hng u ca
                    Ph La.  cng l l do mi dch v, sn phm ca Ph La lun
                    xoay quanh yu t con ngi. Ph La tin rng, khch hng - hay
                    rng hn l cng ng s lun st cnh cng thng hiu v lan ta
                    nhng gi tr tt p n x hi.
                </p>
            </div>
            <div className="image">
                <img
                    src="https://phela.vn/wp-content/uploads/2023/04/shot-canh-toan_1.188.3-2.jpg"
                    alt="Quan Tm"
                />
                <div className="dot"></div>
            </div>
        </div>
        <div className="section reverse">
            <div className="image">
                <img
                    src="https://phela.vn/wp-content/uploads/2023/04/306747764_452254516847273_1841154552179463204_n.jpg"
                    alt="Trch Nhim"
                />
                <div className="dot"></div>
            </div>
            <div className="text">
                <h4>Trch Nhim</h4>
                <p>
                    Ph La mong mun to ra du n mnh m cho  Long c sn khi st
                    cnh cng ngi nng dn v  cao s pht trin bn vng ca
                    ngun nguyn liu. Bi chng ti tin rng y s l bc m vng
                    chc gip Ph La gii thiu rng ri c sn ca ngi Vit ti
                    cng ng v vn ra th gii.
                </p>
            </div>
        </div>
    </div>
);

const Achievements = () => (
    <section className="achievements">
        <h4>Thnh tu ni bt v ng t ho</h4>
        <div className="section-about-us">
            <div className="container">
                <div className="row">
                    <div className="about-us">
                        <AchievementItem
                            imgSrc="https://phela.vn/wp-content/uploads/2021/09/eyes.png"
                            title="210.000+"
                            description="sn phm c bn ra trn th trng trong vng 5 thng kinh doanh trong tnh hnh dch bnh cng thng"
                        />
                        <AchievementItem
                            imgSrc="https://phela.vn/wp-content/uploads/2021/09/ezgif-7-dec41cab7982.png"
                            title="5836"
                            description="ln c nhc n trn Facebook v Instagram trong vng 4 thng (t 15/03/2021 n 15/07/2021) (theo Sprout Social)"
                        />
                        <AchievementItem
                            imgSrc="https://phela.vn/wp-content/uploads/2021/09/Dm.png"
                            title="98%"
                            description="khch hng hi lng v cht lng sn phm v dch v ca Ph La (theo nh gi trn Baemin v Shopee Fresh)"
                        />
                    </div>
                </div>
            </div>
        </div>
    </section>
);

const AchievementItem = ({imgSrc, title, description}) => (
    <div className="achievement-item">
        <div className="img">
            <img decoding="async" src={imgSrc} alt=""/>
        </div>
        <div className="content">
            <h5>{title}</h5>
            <p>{description}</p>
        </div>
    </div>
);

const Timeline = ({ timelineData}) => {
    const sliderRef = useRef(null);

    const settings = {
        dots: true,
        infinite: true,
        speed: 1500,
        slidesToShow: 3,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 5000,
    };

    return (
        <div className="timeline_phela">
            <h4 style={{textAlign: 'center'}}>Nhng du n trong hnh trnh ca chng ti</h4>
            <button className="prev-button" onClick={() => sliderRef.current.slickPrev()}>
                <FaArrowLeft/>
            </button>
            <button className="next-button" onClick={() => sliderRef.current.slickNext()}>
                <FaArrowRight/>
            </button>
            <Slider ref={sliderRef} {...settings}>
                {timelineData.map((item, index) => (
                    <TimelineItem
                        key={index}
                        image={item.image}
                        date={item.date}
                        title={item.title}
                        description={item.description}
                    />
                ))}
            </Slider>
        </div>
    );
};

const TimelineItem = ({image, date, title, description}) => (
    <div className="timeline-item">
        <div className="img">
            <img src={image} alt="" className="timeline_img"/>
            <p className="timeline-date">{date}</p>
        </div>
        <div className="text_content">
            <h6 className="timeline_title">{title}</h6>
            <p className="timeline_description">{description}</p>
        </div>
    </div>
);
export default AboutUs;