import './ContactUs.css';
import Header from "../../components/Header/Header";
import Footer from "../../components/Footer/Footer";
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
const ContactUs = () => {
    return (
        <div>
            <Header/>
            <div className="contact-us">
                <Info/>
                <ContactForm/>
            </div>
            <Footer/>
        </div>
    );
};

const Info = () => (
    <div className="info">

        <div className="form-info">

            <h2>Ph La &#8211; Nt Hng c Sn</h2>

            <div className="item">

                <div className="icon">

                    <img src="https://phela.vn/wp-content/themes/florist/assets/img/75.png"
                         alt=""/>

                </div>

                <div className="text">

                    <p><strong>a ch:</strong></p>

                    <p>Tr s chnh: 289 inh B Lnh, Phng 26, Qun Bnh Thnh, Thnh ph H Ch Minh</p>
                    <p>Chi nhnh  Lt: 7 Nguyn Ch Thanh, phng 1, Thnh ph  Lt, tnh Lm ng</p><p>Chi
                    nhnh H Ni: L 04-9A Khu cng nghip Vnh Hong, phng Hong Vn Th, qun Hong Mai, H
                    Ni</p>

                </div>

            </div>

            <div className="item">

                <div className="icon">

                    <img src="https://phela.vn/wp-content/themes/florist/assets/img/76.png"
                         alt=""/>

                </div>

                <div className="text">

                    <p><strong>Hotline:</strong></p>

                    <p>1900 3013</p>

                </div>

            </div>

            <div className="item">

                <div className="icon">

                    <img src="https://phela.vn/wp-content/themes/florist/assets/img/77.png"
                         alt=""/>

                </div>

                <div className="text">

                    <p><strong>Email:</strong></p>

                    <p>info@phela.vn</p>

                </div>

            </div>

        </div>

    </div>
)


const ContactForm = () => {
    const initialValues = {
        fullName: '',
        email: '',
        content: ''
    };

    const validationSchema = Yup.object({
        fullName: Yup.string().required('Vui lng nhp h v tn'),
        email: Yup.string()
            .email('Email khng hp l')
            .required('Vui lng nhp email'),
        content: Yup.string().required('Vui lng nhp ni dung')
    });

    const handleSubmit = (values, {setSubmitting}) => {
        console.log(values);
        setSubmitting(false);
    };

    return (
        <div className="contact-form-container">
            <h2>Lin h</h2>
            <p className="form-description">
                Vui lng in y  thng tin theo yu cu  chng ti c th h tr qu khch tt nht.
            </p>

            <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                onSubmit={handleSubmit}
            >
                {({isSubmitting}) => (
                    <Form>
                        <div className="form-group">
                            <Field
                                type="text"
                                name="fullName"
                                placeholder="H v tn*"
                                className="form-input"
                            />
                            <ErrorMessage name="fullName" component="div" className="error-message"/>
                        </div>

                        <div className="form-group">
                            <Field
                                type="email"
                                name="email"
                                placeholder="Email*"
                                className="form-input"
                            />
                            <ErrorMessage name="email" component="div" className="error-message"/>
                        </div>

                        <div className="form-group">
                            <Field
                                as="textarea"
                                name="content"
                                placeholder="Ni dung"
                                className="form-textarea"
                            />
                            <ErrorMessage name="content" component="div" className="error-message" />
                        </div>

                        <button
                            type="submit"
                            className="submit-button"
                            disabled={isSubmitting}
                        >
                            Gi
                        </button>
                    </Form>
                )}
            </Formik>
        </div>
    );
};


export default ContactUs;